import React, { useState, useEffect } from "react";
import Layout from "./Layout";
import { contentsHandler } from "../util/handler";
import { useDispatch, useSelector } from "react-redux";

const styleSheet = {
  container: {
    background: "black",
    height: "1000px",
    color: "white",
    textAlign: "center",
    paddingTop: "30px",
    fontSize: "4em"
  }
};
const View = ({ data }) => {
  const lyricsData = useSelector(state => state.lyrics);
  const dispatch = useDispatch();
  const [lyrics, setLyrics] = useState(contentsHandler(data));
  const dataHandler = () => {
    console.log(lyrics);
    return lyrics[0].contents[1];
  };
  return (
    <Layout title="View">
      <div style={styleSheet.container}>
        {dataHandler()
          .split("\n")
          .map((it, i) => {
            return (
              <span key={i}>
                {it}
                <br />
              </span>
            );
          })}
      </div>
    </Layout>
  );
};

export default View;
